<template>
	<div class="project-list">
		<el-form :model="query" :inline="true" ref="searchForm" size="small">
			<el-form-item>
				<el-input v-model="query.name" placeholder="客户名称/联系人" class="w150"></el-input>
			</el-form-item>
			<el-form-item>
				<el-select v-model="query.industry_type_id" class="w150" id="keyDown" size="small" placeholder="">
					<el-option v-for="(item,index) in industryType" :key="index" :label="item.label" :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item>
				<el-select v-model="query.customer_level" class="w150" id="keyDown" size="small" placeholder="">
					<el-option v-for="(item,index) in customerLevel" :key="index" :label="item.label" :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item>
				<el-input v-model="query.director_name" placeholder="负责人姓名" class="w150"></el-input>
			</el-form-item>
			<el-form-item label="计划时间:" prop="">
				<el-date-picker v-model="dateRange" type="daterange" align="right" placeholder="选择日期范围" class="w240" range-separator="至 " :clearable="false" @change="onRangeChange" size="small">
				</el-date-picker>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="onSearch" size="small">查询</el-button>
				<el-button @click="onReset" size="small">清空</el-button>
			</el-form-item>
		</el-form>
		<div class="mb20">
			<el-button type="primary" @click="toNewCustomer" size="small">新建客户</el-button>
			<el-button @click="exportExcel" size="small">导出</el-button>
		</div>
		<el-table :data="tableParams.list" header-align="center" border header-row-class-name="header-center">
			<el-table-column label="客户名称" prop="customer_name"></el-table-column>
			<el-table-column label="联系人" prop="linkman_name"></el-table-column>
			<el-table-column label="所属行业" prop="industry_type_name">
			</el-table-column>
			<el-table-column label="所在地" prop="city_name">
			</el-table-column>
			<el-table-column label="客户级别" prop="customer_level">
				<template slot-scope="scope">
							<span>{{scope.row.customer_level|customerLevel}}</span>
				</template>
            </el-table-column>
			<el-table-column label="负责人" prop="director_name">
			</el-table-column>
			<el-table-column label="创建时间" prop="create_time">
			<template slot-scope="scope">
	<span>{{scope.row.create_time|dateFormat}}</span>
		</template>
            </el-table-column>
			<el-table-column label="操作" fixed="right" width="180" class="tc">
		<template slot-scope="scope">
	<el-button type="text" @click="toOpen" size="mini">
		服务开通</el-button>
	<el-button type="text" size="mini" @click.native="toEdit(scope.row)">
		编辑</el-button>
	<el-button type="text" @click="toDel" size="mini">删除</el-button>
</template>
      		</el-table-column>
    	</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableParams.currentPage" :page-sizes="tableParams.pageSizeList" :page-size="tableParams.pageSize" :layout="tableParams.layout" :total="tableParams.totalCount" v-if="tableParams.totalCount" class="mt15">
		</el-pagination>
  <modal-detail v-model="detailVisible" @reload-list="_loadList"></modal-detail>
  </div>
</template>

<script>
import apis from "@/api";
import tableMixins from "@/mixins/tableMixin";
import { customerLevel } from "@/views/const";
import { fmt, removeEmpty } from "@/utils/util";
import modalDetail from "./modalDetail";
let tableData = [
  {
    id: "11",
    customer_name: "11",
    linkman_name: "11",
    industry_type_name: "11",
    city_name: "11",
    customer_level: "11",
    director_name: "11",
    create_time: "11",
    social_credit_code: "11",
    industry_type_id: "11",
    industry_subtype_id: "11",
    linkmans: "11",
    province_id: "11",
    address: "11",
    website: "11",
    turnover: "11",
    staff_num: "11",
    primary_business: "11",
    business_licence_img: "11",
    attaches: "11",
    customer_summary: "11"
  }
];
export default {
  data() {
    return {
      industryType: [],
      dateRange: "",
      customerLevel,
      query: {
        name: "",
        industry_type_id: "",
        customer_level: "",
        director_name: "",
        create_time: null
      },
      detailVisible: false
    };
  },
  mixins: [tableMixins],
  created() {},
  components: {modalDetail},
  methods: {
    async _loadList() {
      let vm = this;
      let params = removeEmpty(Object.assign({}, vm.query));
      let result = await apis.customer.getCustomerList(vm.assignQuery(params));
      console.log(result);
      // let result={
      // 	total:1,
      // 	rows:tableData
      // }
      vm.initialTableData(result.rows, result.total);
    },
    toEdit(row) {
      this.$router.push({
        name: "projectEdit",
        params: {
          planId: row.id
        }
      });
    },
    async toDel(row) {
      let vm = this;
      let check = apis.customer.checkIsDelCustomer(row.id);
      if (check.result) {
        let result = apis.customer.delCustomerById(row.id);
        vm.$alert("");
      }
    },
    toOpen() {},
    //改变时间范围返回查询时间
    onRangeChange(times) {
      this.query.startTime = times[0] || "";
      this.query.endTime = times[1] || "";
    },
    //导出
    exportExcel() {},
    //新建客户
    toNewCustomer() {
      this.detailVisible = true;
	}
  }
};
</script>


